/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: max-content;
  background-color: black;
  color: #fff;
  text-align: left;
  padding: 5px 15px;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.square {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to bottom right,
    transparent 0%,
    transparent 50%,
    #0078d7 50%,
    #0078d7 100%
  );
}

.square.morning {
  background-image: linear-gradient(
    to bottom right,
    #0078d7 0%,
    #0078d7 50%,
    transparent 50%,
    transparent 100%
  );
}

.square.afternoon {
  background-image: linear-gradient(
    to bottom right,
    transparent 0%,
    transparent 50%,
    #0078d7 50%,
    #0078d7 100%
  );
}

.square.both {
  background-image: linear-gradient(
    to bottom right,
    #0078d7 0%,
    #0078d7 50%,
    #0078d7 50%,
    #0078d7 100%
  );
}
